.description {
  background-color: @basik_white;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  padding-left: 18px;
  padding-right: 18px;
  padding-bottom: 16px;
  padding-top: 5px;
  margin: 0 auto;
  max-width: 400px;

  @media (min-width: @tablet-width) {
    padding: 0;
    padding-bottom: 40px;
    padding-top: 13px;
  }
}


@media (min-width: @tablet-width) {
  .description--form {
    max-width: 435px;
  }

  .description--catalog {
    max-width: 458px;
  }

  .description--index {
    max-width: 484px;
  }

  .description__title {
    font-size: 21px;
  }
}

.description__title {
  color: @basik_black;
  font-weight: 700;
  font-size: 18px;
}

.description__subtitle {
  color: @darkgray;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
}

.description__title--ask {
  padding-top: 10px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    padding-top: 13px;
  }
}

.description--popap-error {
  padding: 0;
  width: 220px;

  @media (min-width: @tablet-width) {
    width: 310px;
  }
}

.description__title--popap-error {
  margin: 0;
  padding-top: 39px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 40px;
    width: 220px;
    padding-left: 45px;
    padding-right: 45px;
    padding-top: 49px;
  }
}

.description__subtitle--popap-error {
  padding-bottom: 23px;
  padding-top: 26px;
  margin: 0;

  @media (min-width: @tablet-width) {
    padding-bottom: 36px;
  }
}

.description--popap-sent {
  padding: 0;
  width: 220px;

  @media (min-width: @tablet-width) {
    width: 444px;
  }
}

.description__title--popap-sent {
  margin: 0;
  padding-top: 39px;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 40px;
    padding-top: 49px;
  }
}

.description__subtitle--popap-sent {
  padding-bottom: 38px;
  padding-top: 26px;
  margin: 0;

  @media (min-width: @tablet-width) {
    padding-bottom: 36px;
  }
}
