}
.page-header__photo--index::before {
  content: "";
  min-width: 282px;
  min-height: 214px;
  position: absolute;
  background-image: url("../img/img_index/text-index.svg");
  background-size: cover;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
